@import url(//netdna.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css);
body {
    margin: 0;
    font-family: monospace;
    background-color: whitesmoke;
  }
  
  .navbar {
    font-family: fantasy;
    font-size: 10px;
    min-height: 75px;
    background-image: -webkit-gradient(linear, right bottom, left top, from(#3761ae), color-stop(#32538b), color-stop(#2e4469), color-stop(#2b3548), to(#262729));
    background-image: linear-gradient(to left top, #3761ae, #32538b, #2e4469, #2b3548, #262729);
    right: 0;
    text-indent: 5px;
    border-radius: 0%;
  }
  
  .sidenav {
    margin-top: calc(100% - 75px);
    bottom: 0;
    left: 0;
    position: fixed;
    float: left;
  }
  
  .content {
    margin-left: 250px;
    margin-right: 100px;
    padding-top: 70px;
    padding-bottom: 80px;
    text-align: center;
    font-size: 50px;
    position: relative;
  }

  .row_cont {
    padding-top: 20px;
    padding-right: 20px;
  }

  .about_info {
    margin-bottom: 50px;
    text-align: center;
    font-size: 10px;
    background-color: seashell;
  }

  .about_indiv_info {
    text-align: center;
    font-size: 30px;
  }

  .about_more_info {
    text-align: center;
    font-size: 15px;
  }

  .info {
    content: "\e086";
  }

  .image {
    display: block;
    text-align: center;
    width: 400px;
    height: 400px;
    margin-left: auto;
    margin-right: auto;
  }

